<template>
  <div class="login">
    <van-nav-bar title="登录" left-arrow @click-left="goPage('My')"></van-nav-bar>

    <van-form>
      <van-field
        v-model="userInfo.phone"
        label-width="1.5rem"
        placeholder="(请输入11位手机号)"
        maxlength="11"
        type="text"
        label="手机号"
        colon
      />

      <van-field
        v-model="code"
        center
        clearable
        label="验证码"
        colon
        label-width="2rem"
        placeholder="(请输入验证码)"
        label-align="right"
      >
        <template #button>
          <van-button @click="refreshCode">
            <Sidentify :identifyCode="identifyCode"></Sidentify>
          </van-button>
        </template>
      </van-field>

      <van-field
        v-model="userInfo.password"
        type="text"
        label="密码"
        colon
        label-width="50px"
        placeholder="(请输入6位以上数字或字母)"
        maxlength="16"
        :type="isEye ? 'text' : 'password'"
        :right-icon="isEye ? 'eye-o' : 'closed-eye'"
        @click-right-icon="PasswordEye"
      />
      <!-- 如果输入框为空 -->
      <div class="login-btn" v-if="userInfo.phone == '' || code == ''|| userInfo.password ==''">
        <van-button
          block
          type="info"
          disabled
          @click="login"
          native-type="submit"
        >登 录</van-button>
      </div>
      <!-- 输入框不为空 -->
      <div class="login-btn" v-else>
        <van-button
          block
          type="info"
          @click="login"
          native-type="submit"
        >登 录</van-button>
      </div>
    </van-form>

    <div class="title">
      <div class="title_nav fl">
        <a href="javascript:;" @click="goPage('Forgot')" class="fr">忘记密码 ?</a>
      </div> 
      <i class="line fl"></i>
      <div class="title_nav fl">
        <a href="javascript:;" @click="goPage('Register')">立即注册 ?</a>
      </div>
    </div>

    <div class="bottom">
      <van-divider
        :style="{ color: 'rgb(143, 143, 143)', borderColor: '#8B8683', padding: '0 50px',fontSize:'13px' }"
      >其他登录方式</van-divider>
      <div class="loginMethod clearfix">
        <div class="loginbox fl">
          <div class="loginimg">
            <img
              src="data:image/png;base64,"
            />
          </div>
          <span>QQ</span>
        </div>
        <div class="loginbox fl">
          <div class="loginimg">
            <img
              src="data:image/png;base64,"
            />
          </div>
          <span>微信</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//导入验证表单模块
import validForm from "../assets/js/validForm";
//导入验证码模块
import Sidentify from "../components/Sidentify.vue";

export default {
  components: {
    Sidentify,
  },
  data() {
    return {
      checked: false,
      //用户注册信息
      userInfo: {
        phone: "",
        password: "",
      },
      //false: 闭合，true：睁开
      isEye: false,
      identifyCodes: "1234567890",
      identifyCode: "",
      code: "",
    };
  },

  methods: {
    //切换密码的格式
    PasswordEye() {
      this.isEye = !this.isEye;
    },

    //跳转页面
    goPage(name) {
      this.$router.push({ name });
    },
    //登录
    login() {
      //检验验证码是否正确
      if (this.code == "") {
        this.$toast("请输入验证码");
        return;
      } else if (this.identifyCode !== this.code) {
        this.code = "";
        this.refreshCode();
        this.$toast("请输入正确的验证码");
        return;
      } else {
        //进行验证表单
        if (validForm.validUserForm(this.userInfo)) {
          //复制对象
          let data = Object.assign({}, this.userInfo);

          //请求密钥
          data.appkey = "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=";

          //开启加载提示
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,

            //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
            duration: 0,
          });

          //发起注册请求
          this.axios({
            method: "POST",
            url: "http://www.kangliuyong.com:10002/login",

            //post传递参数需要写在data
            data,
          })
            .then((result) => {
              //关闭加载提示
              this.$toast.clear();

              if (result.data.code == 200) {
                //跳转到菜单

                //存储token，用于后面登录验证
                localStorage.setItem("__Tk", result.data.token);
                this.$router.push({ name: "Home" });
              } else {
                this.$toast(result.data.msg);
              }
            })
            .catch((err) => {
              //关闭加载提示
              this.$toast.clear();
            });
        }
      }
    },
    //验证码
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },

    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
          this.randomNum(0, this.identifyCodes.length)
        ];
      }
    },
  },
  created() {
    //初始化验证码
    this.refreshCode();
  },
  mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
  },
};
</script>

<style lang="less" scoped>
.login {
  /deep/ .van-nav-bar__title {
    font-size: 18px;
  }
  /deep/.van-nav-bar__left {
    /deep/.van-nav-bar__arrow {
      font-size: 20px;
      color: black;
    }
  }
  /deep/ .van-field__label {
    color: rgb(115, 115, 115);
  }
  .van-field {
    width: 90%;
    margin: 27px 5%;
    border: 1px solid rgb(166, 166, 166);
    border-radius: 3px;
  }
  .van-cell:nth-child(2) {
    padding: 0;
  }
  .van-button--block {
    font-size: 15px;
    height: 44px;
    border-radius: 1rem;
    background-image: linear-gradient(90deg, #f10000, #ff2000 73%, #ff4f18);
  }

  /deep/ .van-icon-success {
    font-size: 15px;
  }
  /deep/.van-field__button {
    padding-left: 0;
  }
  .login-btn {
    padding: 5px 20px;
  }
  .van-button--normal {
    border: 0;
    padding: 0;
  }
  .van-button--info {
    border: 0;
  }
  /deep/ .van-icon-closed-eye {
    color: #ccc;
  }
  /deep/ .van-icon-eye-o {
    color: #ccc;
  }
  .title {
    width: 100%;
    padding: 25px 0;
    .title_nav {
      width: 49%;
      a {
        color: rgb(143, 143, 143);
        margin: 0 15px;
      }
    }
    .line {
      margin: 2px 0;
      height: 13px;
      width: 1px;
      background-color: rgb(109, 108, 108);
    }
  }
  .bottom {
    margin-top: 1rem;
    .loginMethod {
      margin-left: 29%;
      margin-top: 0.5rem;
      text-align: center;
      .loginbox {
        width: 80px;
        .loginimg {
          img {
            width: 1.2rem;
            margin-bottom: 5px;
          }
        }
        span {
          font-size: 13px;
          color: rgb(143, 143, 143);
        }
      }
    }
  }
  @media screen and (max-width: 330px) {
    /deep/.van-field__label--right {
      padding-right: 0;
    }
    /deep/.van-field__label {
      margin-right: 0;
    }
    .bottom {
      .loginbox {
        margin-left: -3%;
      }
    }
  }
}
</style>